html {
  background-color: #86b049;
}

#keyboard-cont {
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#keyboard-cont div {
  display: flex;
}

.second-row {
  margin: 0.5rem 0;
}

.keyboard-button {
  color: #c8b88a;
  border: whitesmoke;
  background-color: rgb(71, 105, 48);
  font-size: 1rem;
  font-weight: 700;
  padding: .6rem;
  margin: 0 2px;
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 10px;
}

.row-cont {
  display: flex;
  justify-content: center;
}

.row {
  padding-top: 10px;
  display: inline-grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 4px;
  grid-auto-flow: column;

}

.box {
  display: flex;
  width: 65px;
  height: 65px;
  background-color: #3c6f3c;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  color: whitesmoke;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}

.active {
  border: 1px solid #f1dddf;
}

.error {
  color: whitesmoke;
  font-weight: 600;
  font-size: 25px;
  padding: 0px;
  margin: 0px;
  text-align: center;
}

.grey{
  background-color: rgb(128, 128, 128);
}

.green{
  background-color: rgb(119, 221, 119);
}

.yellow{
  background-color: rgb(223, 230, 103);
}

.title-image{
  width: 75px;
  height: 75px;
  float: left; 
  margin-right: 10px; 
}

.title-image2{
  width: 75px;
  height: 75px;
  float: right; 
  margin-left : 10px; 
}

.header{
  margin-bottom: 10px;
  padding-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header h4 {
  color: #234f1e
}

.next-button{
  margin: 0;
  position: absolute;
  padding: 10px;
  border-radius: 5px;
  left: 50%;
  transform: translate(-50%, 30%);
  background-color: #476930;
  color:#c8b88a;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: 500;
}

.hidden{
  display: none;
}
